@import url('https://fonts.googleapis.com/css2?family=Andika&family=Inter:wght@400;600;800&family=Montserrat:wght@300;500&family=Playfair+Display:wght@700&family=Poppins:wght@300;500;700&family=Roboto&family=Roboto+Condensed&display=swap');

.About {
    display: flex;
    margin: auto;
    margin-top: 100px;
    font-family: 'Poppins', 'sans-serif';
    width: 70%;
    gap: 30px;
    flex-direction: column;
    position: relative;

}

.About>div:nth-child(2)>div:nth-child(1) {
    width: 50%;
    color: #8892B0;
}

.About>div:nth-child(1) {
    display: flex;
    align-items: center;
    gap: 10px;
    color: #CCD6F6;
}

.About>div:nth-child(1)>div {
    width: 250px;
    border: 1px solid #8892B0;
}

.About>div:nth-child(2) {
    display: flex;
    gap: 60px;
}

.About>div:nth-child(2)>div:nth-child(1)>div:nth-child(1) {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    color: #8892B0;
}

.About>div:nth-child(2)>div:nth-child(1)>div:nth-child(1)>p {
    text-align: left;
}

.About>div:nth-child(2)>div:nth-child(1)>div:nth-child(2) {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
}

.About>div:nth-child(2)>div:nth-child(1)>div:nth-child(2)>p {
    text-align: left;
}

.About>div:nth-child(2)>div:nth-child(1)>div:nth-child(3) {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.About>div:nth-child(2)>div:nth-child(1)>div:nth-child(3)>div {
    display: flex;
    gap: 10px;
}

.About>div:nth-child(2)>div:nth-child(1)>div>a>button {
    padding: 15px 0px;
    border: 1px solid #5fe4c9;
    background-color: transparent;
    color: #5fe4c9;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    width: 150px;
    margin-top: 15px;
}

.About>div:nth-child(2)>div:nth-child(1)>div:nth-child(3)>p {
    text-align: left;
}

.About>div:nth-child(2)>div:nth-child(1)>div:nth-child(3)>div {
    gap: 20px;
}

.About>div:nth-child(2)>div:nth-child(1)>div:nth-child(3)>div>div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: #5fe4c9;
    align-items: flex-start;
    text-align: left;
    line-height: 3px;
}

.About>div:nth-child(2)>div:nth-child(1)>div:nth-child(3)>div>div>p {
    list-style-type: none;
    text-align: left;
}

/* for images */

.profile {
    height: fit-content;
    position: relative;

}

.profile>div {
    width: 300px;
    height: 300px;

}

.profile:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 20em;
    height: 20em;

}

.profile>div>img {
    object-fit: cover;
    border-radius: 5px;
    width: 100%;
    height: 100%;
    margin-top: 18px;
}

.profile>div>div {
    object-fit: cover;
    position: absolute;
    border: 3px solid #5fe4c9;
    border-radius: 5px;
    z-index: -1;
    width: 300px;
    height: 300px;
    padding: 5px;
    top: 30px;
    left: 10px;
}

.profile>div>:hover {
    left: 5px;
    cursor: pointer;
}

@media screen and (min-width:420px)and (max-width:1024px) {
    .About {
        width: 80%;

    }

    .About>div:nth-child(2) {
        flex-direction: column;
    }

    .About>div:nth-child(2)>div:nth-child(1) {
        width: 100%;
    }
}

@media screen and (min-width:221px)and (max-width:420px) {
    .About {
        width: 90%;
    }

    .About>div:nth-child(2) {
        flex-direction: column;
    }

    .About>div:nth-child(1)>div {
        width: 100px;
    }

    .About>div:nth-child(1)>h2 {
        font-size: 20px;
    }

    .About>div:nth-child(2)>div:nth-child(1) {
        width: 100%;
    }
}