@import url('https://fonts.googleapis.com/css2?family=Andika&family=Inter:wght@400;600;800&family=Montserrat:wght@300;500&family=Playfair+Display:wght@700&family=Poppins:wght@300;500;700&family=Roboto&family=Roboto+Condensed&display=swap');

.Home {
    width: 70%;
    margin: auto;
    margin-top: 150px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
    font-family: 'Poppins', 'sans-serif';
}

.Home>p {
    font-family: 'Andika', 'sans-serif';
    color: #5fe4c9;
}

.Home>div:nth-child(2) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    line-height: 13px;
}

.Home>div:nth-child(3) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
    width: 100%;
}

.Home>div:nth-child(3)>p {
    color: #8892B0;
    width: 60%;
}

.Home>div:nth-child(2)>h1:nth-child(1) {
    font-size: 65px;
    color: #CCD6F6;
}

.Home>div:nth-child(2)>h1:nth-child(2) {
    font-size: 65px;
    color: #8892B0;
}

.Home>div:nth-child(4)>button {
    padding: 15px 20px;
    border: 1px solid #5fe4c9;
    background-color: transparent;
    color: #5fe4c9;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    margin-top: 30px;
    width: 150px;
}

.Home>div:nth-child(4)>button>a {
    text-decoration: none;
    color: #5fe4c9;

}

/* for medium screen */
@media screen and (min-width:420px)and (max-width:1224px) {
    .Home {
        width: 80%;
    }

    .Home>div:nth-child(2)>h1:nth-child(1) {
        font-size: 45px;
    }

    .Home>div:nth-child(2) {
        flex-wrap: nowrap;
    }

    .Home>div:nth-child(2)>h1:nth-child(2) {
        width: 100%;
        text-align: left;
        font-size: 45px;
        line-height: 45px;
        margin-top: 8px;
    }

    .Home>div:nth-child(3)>p {
        width: 100%;
        font-size: 14px;
    }


}

/* for mobile screen */
@media screen and (min-width:221px)and (max-width:420px) {
    .Home {
        width: 90%;
    }

    .Home>div:nth-child(2)>h1:nth-child(1) {
        font-size: 30px;
    }

    .Home>div:nth-child(2) {
        height: 140px;
        line-height: 0px;
        flex-wrap: nowrap;
    }

    .Home>div:nth-child(2)>h1:nth-child(2) {
        width: 100%;
        text-align: left;
        font-size: 30px;
        line-height: 45px;
        margin-top: 8px;
    }

    .Home>div:nth-child(3)>p {
        width: 100%;
        font-size: 13px;
    }

    .Home>div:nth-child(4)>button {
        width: 100px;
        font-size: 12px;
    }


}