.calendar {
    background-color: #000000;
    color: white;
    border: none;
    padding: 10px;
    border: 2px solid red
}

.gitCalendar {
    width: 70%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin-top: 60px;
    gap: 40px;
}

.gitCalendar>div:nth-child(1) {
    display: flex;
    align-items: center;
    gap: 10px;
    color: #CCD6F6;
}

.gitCalendar>div:nth-child(1)>div {
    width: 250px;
    border: 1px solid #8892B0;
}

@media screen and (min-width:320px)and (max-width:600px) {
    .callu {
        width: 90% !important;
        margin: auto;
    }
}

@media screen and (min-width:420px)and (max-width:1024px) {
    .gitCalendar {
        width: 85%;

    }

    .callu {
        width: 100% !important;
        margin: auto;
    }

    .gitCalendar>div:nth-child(1)>div {
        width: 100px;
    }

    .gitCalendar>div:nth-child(1)>h2 {
        font-size: 20px;
    }

}

@media screen and (min-width:221px)and (max-width:420px) {
    .gitCalendar {
        width: 90%;

    }

    .callu {
        width: 90% !important;
        margin: auto;
    }

    .gitCalendar>div:nth-child(1)>div {
        width: 100px;
    }

    .gitCalendar>div:nth-child(1)>h2 {
        font-size: 20px;
    }

    .gitStats {
        width: 320px;
    }

}