@import url('https://fonts.googleapis.com/css2?family=Andika&family=Inter:wght@400;600;800&family=Montserrat:wght@300;500&family=Playfair+Display:wght@700&family=Poppins:wght@300;500;700&family=Roboto&family=Roboto+Condensed&display=swap');

.navbar {
    background-color: #000000;
    justify-content: space-between;
    width: 100%;
    display: flex;
    align-items: center;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    position: fixed;
    top: 0;
    z-index: 1000;
}

.navbar>div:nth-child(2) {
    padding: 0px 10px;
}

.navbar>div:nth-child(2)>img {
    width: 100px;
}

.navbar>div:nth-child(3) {
    width: 50%;
    padding: 0px 10px;
}

.navbar>div:nth-child(3)>ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.navbar>div:nth-child(3)>ul>button {
    padding: 15px 20px;
    border: 1px solid #5fe4c9;
    background-color: transparent;
    color: #5fe4c9;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
}

.navbar>div:nth-child(3)>ul>button>a {
    text-decoration: none;
    color: #5fe4c9;
}

.navbar>div:nth-child(3)>ul>.nav-link>li {
    color: #8892B0;
    text-decoration: none;
    list-style: none;
    font-size: 14px;
    font-family: 'Andika', sans-serif;
}


.navbar>div:nth-child(3)>ul>.nav-link {
    text-decoration: none;
}

.navbar>div:nth-child(3)>ul>.nav-link>li:hover {
    color: #5fe4c9;
    cursor: pointer;
}

.progress-bar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 8px;
    border-radius: 100px;
    background: #5fe4c9;
    transform-origin: 0%;
}

.bm-item-list>a {
    text-decoration: none;
    color: #8892B0;
    font-size: 16px;
    font-family: 'Andika', sans-serif;
}

.bm-item-list>a>button {
    padding: 15px 20px;
    border: 1px solid #5fe4c9;
    background-color: transparent;
    color: #5fe4c9;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    width: 100px;
    margin-top: 15px;
}

.bm-burger-button>span>span:nth-child(1) {
    height: 10%;
}

.bm-burger-button>span>span:nth-child(2) {
    height: 10%;
    width: 65%;
}

.bm-burger-button>span>span:nth-child(3) {
    height: 10%;
}