@import url('https://fonts.googleapis.com/css2?family=Andika&family=Inter:wght@400;600;800&family=Montserrat:wght@300;500&family=Playfair+Display:wght@700&family=Poppins:wght@300;500;700&family=Roboto&family=Roboto+Condensed&display=swap');

.App {
  text-align: center;
}

body {
  background-color: #000000;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #000000;
}



.loader>div>img {
  width: 500px;
  height: 500px;
}

.App>div:nth-child(10) {
  color: #CCD6F6;
  padding: 10px;
  margin-top: 60px;
  font-family: "Andika", "sans-serif";

}

::-webkit-scrollbar {
  width: 20px;
  z-index: -1;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #5fe4c9;
  border-radius: 20px;
  border: 6px solid transparent;
  background-clip: content-box;
}

